___CSS_LOADER_EXPORT___.push([module.id, "@keyframes wave-bg-7e803eea{0%,to{background-color:#dbeafe}50%{background-color:#bfdbfe}}.wave-background[data-v-7e803eea]{animation:wave-bg-7e803eea 5s ease-in-out infinite;background-color:#dbeafe}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Shows/{$id}/Manage.vue"],"names":[],"mappings":"AA8SA,4BACE,MACE,wBACF,CACA,IACE,wBACF,CACF,CAEA,kCACE,kDAA0C,CAC1C,wBACF","sourcesContent":["<template>\n\n  <Head :title=\"`Manage Show: ${props.show.name}`\"/>\n\n  <div id=\"topDiv\" class=\"place-self-center flex flex-col\">\n    <div class=\"min-h-screen bg-white text-black dark:bg-gray-900 dark:text-gray-50 rounded px-5\">\n\n      <Message v-if=\"appSettingStore.showFlashMessage\" :flash=\"$page.props.flash\"/>\n      <div class=\"alert alert-error mt-4 mx-3\"\n           v-if=\"showStore.errorMessage\">\n        <svg xmlns=\"http://www.w3.org/2000/svg\" class=\"stroke-current shrink-0 h-6 w-6\" fill=\"none\" viewBox=\"0 0 24 24\">\n          <path stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\"\n                d=\"M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z\"/>\n        </svg>\n        <span>{{ showStore.errorMessage }}</span>\n        <button class=\"text-xs ml-12\" @click=\"showStore.errorMessage = ''\"> Close</button>\n      </div>\n\n      <header class=\"bg-gradient-to-r from-blue-100 via-white to-transparent p-4 text-black font-bold rounded-lg\">\n        <!--            <header class=\"wave-background p-4 text-black font-bold rounded-lg\">-->\n\n        <div class=\"flex justify-between mb-3 pt-6\">\n          <div class=\"font-bold mb-4 text-black align-bottom text-lg\">MANAGE SHOW</div>\n\n          <div class=\"flex flex-col\">\n            <div class=\"flex flex-row w-full justify-end mb-2\">\n              <button\n                  @click=\"appSettingStore.btnRedirect(`/teams/${team.slug}/manage`)\"\n                  class=\"px-4 py-2 mr-2 mb-2 h-fit text-white font-semibold bg-orange-500 hover:bg-orange-600 rounded-lg\"\n              >\n                <font-awesome-icon :icon=\"['fas', 'users-cog']\" class=\"hover:text-blue-800 mr-1\"/>\n                Manage Team\n              </button>\n            </div>\n            <div class=\"flex flex-wrap-reverse justify-end gap-2\">\n              <button\n                  v-if=\"can.goLive\"\n                  @click=\"goLive\"\n                  class=\"px-4 py-2 h-fit text-white font-semibold bg-red-500 hover:bg-red-600 rounded-lg\"\n              >\n                <font-awesome-icon :icon=\"['fas', 'broadcast-tower']\" class=\"hover:text-blue-800 mr-1\"/>\n                Go Live\n              </button>\n              <button\n                  @click=\"appSettingStore.btnRedirect(`/shows/${show.slug}`)\"\n                  class=\"px-4 py-2 h-fit text-white font-semibold bg-blue-500 hover:bg-blue-600 rounded-lg\"\n              >\n                <font-awesome-icon :icon=\"['fas', 'eye']\" class=\"hover:text-blue-800 mr-1\"/>\n                View Show\n              </button>\n              <button\n                  v-if=\"teamStore.can.editShow\"\n                  @click=\"appSettingStore.btnRedirect(`/shows/${show.slug}/edit`)\"\n                  class=\"px-4 py-2 h-fit text-white font-semibold bg-blue-500 hover:bg-blue-600 rounded-lg\"\n              >\n                <font-awesome-icon :icon=\"['fas', 'pen']\" class=\"hover:text-blue-800 mr-1\"/>\n                Edit Show\n              </button>\n\n              <DashboardButton class=\"\"/>\n\n            </div>\n            <div class=\"flex flex-wrap-reverse justify-end gap-2\">\n              <ShortUrlManager />\n            </div>\n          </div>\n\n        </div>\n\n        <ShowManageHeader\n            :show=\"props.show\"\n            :team=\"props.team\"\n            :can=\"can\"\n        />\n\n        <div class=\"flex justify-end mt-6\">\n\n\n        </div>\n      </header>\n\n      <div class=\"my-6 text-gray-100\">\n        <span class=\"uppercase text-xs font-semibold text-black\">SHOW NOTES: </span>\n        <span class=\"text-black light:text-black dark:text-white\">{{ props.show.notes }}</span>\n      </div>\n\n\n      <div class=\"flex flex-col\">\n        <div class=\"-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8\">\n          <div class=\"py-2 align-middle inline-block w-full sm:px-6 lg:px-8 space-y-4\">\n\n            <div @click=\"toggleComponent('showEpisodes')\"\n                 :class=\"{'rounded-t-lg': showStore.openComponent === 'showEpisodes', 'rounded-lg': showStore.openComponent !== 'showEpisodes'}\"\n                 class=\"accordion-header p-2 font-bold transition duration-300 ease-in-out transform focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 overflow-hidden shadow-lg bg-blue-100 hover:bg-blue-300 dark:hover:bg-blue-900 text-black hover:text-blue-900 dark:text-blue-100 dark:hover:text-white\">\n              <font-awesome-icon icon=\"fa-play-circle\" class=\"\"/>\n              Episodes\n            </div>\n            <div v-if=\"showStore.openComponent === 'showEpisodes'\">\n              <div class=\"mt-4 mb-12 pb-6 shadow overflow-auto border-b border-gray-200 sm:rounded-lg\">\n                <ManageShowEpisodesList/>\n              </div>\n            </div>\n\n            <div @click=\"toggleComponent('showCredits')\"\n                 :class=\"{'rounded-t-lg': showStore.openComponent === 'showCredits', 'rounded-lg': showStore.openComponent !== 'showCredits'}\"\n                 class=\"accordion-header p-2 font-bold transition duration-300 ease-in-out transform focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 overflow-auto shadow-lg bg-blue-100 hover:bg-blue-300 dark:hover:bg-blue-900 text-black hover:text-blue-900 dark:text-blue-100 dark:hover:text-white\">\n              <font-awesome-icon icon=\"fa-clipboard-list\" class=\"\"/>\n              Credits and Assignments\n            </div>\n            <div v-if=\"showStore.openComponent === 'showCredits'\">\n              <div class=\"mt-4 mb-12 pb-6 shadow overflow-auto border-b border-gray-200 sm:rounded-lg\">\n                <div class=\"border-1 border-t mb-3 bg-blue-100 py-1 px-2 text-xs font-semibold text-red-800\">\n                  In development. Not currently working.\n                </div>\n                <Link\n                    :href=\"`#`\"\n                    v-if=\"teamStore.can.createAssignment\">\n                  <button\n                      class=\"bg-green-500 hover:bg-green-600 text-white ml-2 my-2 px-4 py-2 rounded disabled:bg-gray-400 h-max w-max\"\n                      disabled\n                  >Create Assignment\n                  </button>\n                </Link>\n\n                <ShowCreditsList/>\n              </div>\n            </div>\n\n            <div @click=\"toggleComponent('showRecordings')\"\n                 :class=\"{'rounded-t-lg': showStore.openComponent === 'showRecordings', 'rounded-lg': showStore.openComponent !== 'showRecordings'}\"\n                 class=\"accordion-header p-2 font-bold transition duration-300 ease-in-out transform focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 overflow-auto shadow-lg bg-blue-100 hover:bg-blue-300 dark:hover:bg-blue-900 text-black hover:text-blue-900 dark:text-blue-100 dark:hover:text-white\">\n              <font-awesome-icon icon=\"fa-circle\" class=\"text-red-700\"/>\n              Recordings\n            </div>\n            <div v-if=\"showStore.openComponent === 'showRecordings'\">\n              <div class=\"mt-4 mb-12 pb-6 shadow overflow-auto border-b border-gray-200 sm:rounded-lg\">\n                <ShowRecordingsList :showRecordings=\"show.recordings\" :showName=\"show.name\" :showSlug=\"show.slug\"\n                                    :showImage=\"show.image\"/>\n\n                <!--            </div>-->\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n      <ShowFooter :team=\"team\" :show=\"show\"/>\n    </div>\n  </div>\n\n</template>\n\n<script setup>\nimport { onBeforeUnmount, onMounted, onUnmounted, ref } from 'vue'\nimport { usePage } from '@inertiajs/vue3'\nimport { usePageSetup } from '@/Utilities/PageSetup'\nimport { useAppSettingStore } from '@/Stores/AppSettingStore'\nimport { useShowStore } from '@/Stores/ShowStore'\nimport { useTeamStore } from '@/Stores/TeamStore'\nimport { useGoLiveStore } from '@/Stores/GoLiveStore'\nimport ShowManageHeader from '@/Components/Pages/Shows/Layout/ShowManageHeader'\nimport ShowFooter from '@/Components/Pages/Shows/Layout/ShowFooter'\nimport ManageShowEpisodesList from '@/Components/Pages/Shows/Elements/ManageShowEpisodesList'\nimport ShowCreditsList from '@/Components/Pages/Shows/Elements/ManageShowCreditsList'\nimport Message from '@/Components/Global/Modals/Messages'\nimport DashboardButton from '@/Components/Global/Buttons/DashboardButton.vue'\nimport ShowRecordingsList from '@/Components/Pages/ShowRecordings/ShowRecordingsList.vue'\nimport { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'\nimport ShortUrlManager from '@/Components/Global/Url/ShortUrlManager.vue'\n\nusePageSetup('shows/slug/manage')\n\nconst appSettingStore = useAppSettingStore()\nconst showStore = useShowStore()\nconst teamStore = useTeamStore()\nconst goLiveStore = useGoLiveStore()\n\nconst toggleComponent = (componentName) => {\n  showStore.openComponent = showStore.openComponent === componentName ? null : componentName\n}\n\nconst page = usePage().props\n\nconst props = defineProps({\n  show: Object,\n  team: Object,\n  episodes: Object,\n  episodeStatuses: Object,\n  // filters: Object,\n  can: Object,\n})\n\nteamStore.setActiveTeam(props.team)\nteamStore.setActiveShow(props.show)\nteamStore.can = props.can\n\nconst currentUsers = ref([])\n\nconst goLive = () => {\n  goLiveStore.reset()\n  goLiveStore.preSelectedShowId = props.show.id\n  appSettingStore.btnRedirect(`/golive`)\n}\n\nonMounted(() => {\n  showStore.initializeShow(props.show, props.episodes, props.episodeStatuses)\n  console.log(`Subscribing to channel creator.show.${props.show.id}`)\n\n  Echo.join(`creator.show.${props.show.id}`)\n      .here(async (users) => {\n        console.log('Users currently in the channel:', users)\n        currentUsers.value = users\n        // Elect the leader (first user in the list)\n        if (users.length > 0) {\n          const leader = users[0]\n          showStore.setLeader(leader)\n          console.log(`Leader elected: ${leader.name}`)\n        }\n\n        console.log(showStore.isUpdatingSchedule, showStore.updatedBy)\n        // Check if the user is the only one in the channel\n        if (currentUsers.value.length === 1 && showStore.isUpdatingSchedule && showStore.updatedBy) {\n          try {\n            // Reset the isUpdatingSchedule and updatedBy fields\n            await axios.put(`/api/shows/${props.show.slug}/meta`, {\n              isUpdatingSchedule: false,\n              updatedBy: showStore.updatedBy,\n            })\n            console.log('isUpdatingSchedule and updatedBy fields reset')\n\n          } catch (error) {\n            console.error('Error updating meta:', error)\n          }\n        }\n      })\n      .joining((user) => {\n        console.log('User joined the channel:', user)\n        currentUsers.value.push(user)\n      })\n      .leaving((user) => {\n        console.log('User left the channel:', user)\n        currentUsers.value = currentUsers.value.filter(u => u.id !== user.id)\n\n        if (user.id === showStore.leader.id) {\n          console.log('Leader left, electing new leader')\n          // Elect a new leader if the current leader leaves\n          if (currentUsers.value.length > 0) {\n            const newLeader = currentUsers.value[0]\n            showStore.setLeader(newLeader)\n            console.log(`New leader elected: ${newLeader.name}`)\n          }\n        }\n        // If current user is still the leader, run the command\n        if (showStore.leader.id === page.user.id) {\n          axios.post(`/api/${props.show.slug}/user-left-channel`, {\n            user: user,\n            channel: `creator.show.${props.show.id}`,\n            showSlug: props.show.slug, // Pass the show slug\n          }).then(response => {\n            console.log('Successfully posted to user-left-channel:', response.data)\n          }).catch(error => {\n            console.error('Error posting to user-left-channel:', error)\n          })\n        }\n      })\n      .listen('.CreatorContentStatusUpdated', (event) => {\n        console.log('CreatorContentStatusUpdated event received:', event)\n        showStore.$patch({\n          isSaving: event.meta.isSaving,\n          isUpdatingSchedule: event.meta.isUpdatingSchedule,\n          updatedBy: event.meta.updatedBy,\n        })\n      })\n      .listen('.ShowScheduleDetailsUpdated', (event) => {\n        console.log('ShowScheduleDetailsUpdated event received:', event)\n        // Assuming `start_DateTime` needs to be transformed into `startTime`\n        const formattedStartTime = event.scheduleDetails.start_DateTime\n        showStore.scheduleDetails = {\n          ...event.scheduleDetails,\n          startTime: formattedStartTime,\n        }\n        console.log('Updated scheduleDetails.startTime:', showStore.scheduleDetails.startTime)\n      })\n      .error((error) => {\n        console.error('Error subscribing to channel:', error)\n      })\n\n})\n\nonBeforeUnmount(() => {\n  // Echo.leave(`creator.show.${props.show.id}`);\n  Echo.leave(`creator.show.${props.show.id}`)\n\n  showStore.setUpdatingStatus(false, page.user.name, props.show.slug)\n\n  console.log(`Unsubscribed from channel creator.show.${props.show.id}`)\n  showStore.reset()\n})\n\n</script>\n\n\n<style scoped>\n@keyframes wave-bg {\n  0%, 100% {\n    background-color: #dbeafe; /* Original color */\n  }\n  50% {\n    background-color: #bfdbfe; /* Slightly lighter color */\n  }\n}\n\n.wave-background {\n  animation: wave-bg 5s infinite ease-in-out;\n  background-color: #dbeafe; /* Starting color */\n}\n\n\n</style>\n"],"sourceRoot":""}]);
